'use client';

import { useCart } from '@/contexts/cart-context';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';

export const Cart = () => {
  const { state, dispatch } = useCart();

  const updateQuantity = (index: string, newQuantity: number) => {
    dispatch({
      type: 'UPDATE_QUANTITY',
      payload: { id: index, quantity: newQuantity }
    });
  };

  const removeItem = (index: string) => {
    dispatch({
      type: 'REMOVE_ITEM',
      payload: index
    });
  };

  const clearCart = () => {
    dispatch({ type: 'CLEAR_CART' });
  };

  if (state.items.length === 0) {
    return (
      <Card className="sticky top-4">
        <CardHeader>
          <CardTitle className="flex items-center justify-between">
            Your Cart
            <Badge variant="secondary">0 items</Badge>
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="text-center py-8 text-gray-500">
            <div className="text-4xl mb-2">üõí</div>
            <p>Your cart is empty</p>
            <p className="text-sm">Add some delicious pizzas!</p>
          </div>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="sticky top-4">
      <CardHeader>
        <CardTitle className="flex items-center justify-between">
          Your Cart
          <Badge variant="secondary">{state.items.length} items</Badge>
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-4">
        {state.items.map((item, index) => {
          const basePrice = item.pizza.sizes[item.size];
          const toppingsPrice = item.selectedToppings.length * 1.5;
          const itemTotal = (basePrice + toppingsPrice) * item.quantity;

          return (
            <div key={index} className="space-y-3 pb-4 border-b last:border-b-0">
              <div className="flex justify-between items-start">
                <div className="flex-1">
                  <h4 className="font-semibold">{item.pizza.name}</h4>
                  <p className="text-sm text-gray-600 capitalize">
                    {item.size} size
                  </p>
                  {item.selectedToppings.length > 0 && (
                    <div className="mt-1">
                      <p className="text-xs text-gray-500 mb-1">Extra toppings:</p>
                      <div className="flex flex-wrap gap-1">
                        {item.selectedToppings.map((topping, tIndex) => (
                          <Badge key={tIndex} variant="outline" className="text-xs">
                            {topping}
                          </Badge>
                        ))}
                      </div>
                    </div>
                  )}
                </div>
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={() => removeItem(index.toString())}
                  className="text-red-500 hover:text-red-700 hover:bg-red-50"
                >
                  Remove
                </Button>
              </div>

              <div className="flex items-center justify-between">
                <div className="flex items-center space-x-2">
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => updateQuantity(index.toString(), item.quantity - 1)}
                    disabled={item.quantity <= 1}
                  >
                    -
                  </Button>
                  <span className="w-8 text-center font-medium">{item.quantity}</span>
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => updateQuantity(index.toString(), item.quantity + 1)}
                  >
                    +
                  </Button>
                </div>
                <div className="text-right">
                  <div className="font-bold">${itemTotal.toFixed(2)}</div>
                  <div className="text-xs text-gray-500">
                    ${(basePrice + toppingsPrice).toFixed(2)} each
                  </div>
                </div>
              </div>
            </div>
          );
        })}

        <Separator />

        <div className="space-y-3">
          <div className="flex justify-between items-center text-lg font-bold">
            <span>Total:</span>
            <span>${state.total.toFixed(2)}</span>
          </div>

          <div className="space-y-2">
            <Button className="w-full bg-black hover:bg-gray-800 text-white">
              Proceed to Checkout
            </Button>
            <Button
              variant="outline"
              className="w-full"
              onClick={clearCart}
            >
              Clear Cart
            </Button>
          </div>
        </div>
      </CardContent>
    </Card>
  );
};
