'use client';

import { useState } from 'react';
import { Pizza, availableToppings } from '@/data/pizza-data';
import { useCart } from '@/contexts/cart-context';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';
import { Label } from '@/components/ui/label';
import { Checkbox } from '@/components/ui/checkbox';
import { Separator } from '@/components/ui/separator';

interface PizzaCustomizerProps {
  pizza: Pizza;
  isOpen: boolean;
  onClose: () => void;
}

export const PizzaCustomizer = ({ pizza, isOpen, onClose }: PizzaCustomizerProps) => {
  const { dispatch } = useCart();
  const [selectedSize, setSelectedSize] = useState<keyof Pizza['sizes']>('medium');
  const [selectedToppings, setSelectedToppings] = useState<string[]>([]);
  const [quantity, setQuantity] = useState(1);

  const handleToppingToggle = (topping: string) => {
    setSelectedToppings(prev =>
      prev.includes(topping)
        ? prev.filter(t => t !== topping)
        : [...prev, topping]
    );
  };

  const calculatePrice = () => {
    const basePrice = pizza.sizes[selectedSize];
    const toppingsPrice = selectedToppings.length * 1.5;
    return (basePrice + toppingsPrice) * quantity;
  };

  const handleAddToCart = () => {
    dispatch({
      type: 'ADD_ITEM',
      payload: {
        pizza,
        size: selectedSize,
        quantity,
        selectedToppings
      }
    });
    onClose();
    // Reset form
    setSelectedSize('medium');
    setSelectedToppings([]);
    setQuantity(1);
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-2xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle className="text-2xl font-bold">{pizza.name}</DialogTitle>
        </DialogHeader>

        <div className="space-y-6">
          {/* Pizza Image Placeholder */}
          <div className="w-full h-48 bg-gray-100 rounded-lg flex items-center justify-center">
            <div className="text-8xl">üçï</div>
          </div>

          {/* Size Selection */}
          <div className="space-y-3">
            <h3 className="text-lg font-semibold">Choose Size</h3>
            <RadioGroup value={selectedSize} onValueChange={(value) => setSelectedSize(value as keyof Pizza['sizes'])}>
              {Object.entries(pizza.sizes).map(([size, price]) => (
                <div key={size} className="flex items-center space-x-2">
                  <RadioGroupItem value={size} id={size} />
                  <Label htmlFor={size} className="flex-1 cursor-pointer">
                    <div className="flex justify-between items-center">
                      <span className="capitalize font-medium">{size}</span>
                      <span className="font-bold">${price}</span>
                    </div>
                  </Label>
                </div>
              ))}
            </RadioGroup>
          </div>

          <Separator />

          {/* Default Toppings */}
          <div className="space-y-3">
            <h3 className="text-lg font-semibold">Included Toppings</h3>
            <div className="flex flex-wrap gap-2">
              {pizza.toppings.map((topping, index) => (
                <Badge key={index} variant="default" className="bg-gray-900 text-white">
                  {topping}
                </Badge>
              ))}
            </div>
          </div>

          {/* Additional Toppings */}
          <div className="space-y-3">
            <h3 className="text-lg font-semibold">Extra Toppings (+$1.50 each)</h3>
            <div className="grid grid-cols-2 gap-3">
              {availableToppings.map((topping) => (
                <div key={topping} className="flex items-center space-x-2">
                  <Checkbox
                    id={topping}
                    checked={selectedToppings.includes(topping)}
                    onCheckedChange={() => handleToppingToggle(topping)}
                  />
                  <Label htmlFor={topping} className="cursor-pointer text-sm">
                    {topping}
                  </Label>
                </div>
              ))}
            </div>
          </div>

          <Separator />

          {/* Quantity */}
          <div className="space-y-3">
            <h3 className="text-lg font-semibold">Quantity</h3>
            <div className="flex items-center space-x-3">
              <Button
                variant="outline"
                size="sm"
                onClick={() => setQuantity(Math.max(1, quantity - 1))}
                disabled={quantity <= 1}
              >
                -
              </Button>
              <span className="text-xl font-semibold w-8 text-center">{quantity}</span>
              <Button
                variant="outline"
                size="sm"
                onClick={() => setQuantity(quantity + 1)}
              >
                +
              </Button>
            </div>
          </div>

          {/* Price and Add to Cart */}
          <div className="sticky bottom-0 bg-white pt-4 border-t">
            <div className="flex items-center justify-between mb-4">
              <div className="text-2xl font-bold">
                Total: ${calculatePrice().toFixed(2)}
              </div>
            </div>
            <Button
              onClick={handleAddToCart}
              className="w-full bg-black hover:bg-gray-800 text-white py-3 text-lg"
            >
              Add to Cart
            </Button>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
};
