'use client';

import { useState } from 'react';
import { Pizza } from '@/data/pizza-data';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { PizzaCustomizer } from './pizza-customizer';

interface PizzaCardProps {
  pizza: Pizza;
}

export const PizzaCard = ({ pizza }: PizzaCardProps) => {
  const [showCustomizer, setShowCustomizer] = useState(false);

  return (
    <>
      <Card className="h-full hover:shadow-lg transition-shadow duration-200 border-gray-200">
        <CardHeader className="pb-4">
          <div className="w-full h-48 bg-gray-100 rounded-lg mb-4 flex items-center justify-center">
            <div className="text-6xl font-bold text-gray-300">üçï</div>
          </div>
          <CardTitle className="text-xl font-bold text-gray-900">{pizza.name}</CardTitle>
          <CardDescription className="text-gray-600 text-sm leading-relaxed">
            {pizza.description}
          </CardDescription>
        </CardHeader>
        
        <CardContent className="pt-0">
          <div className="space-y-4">
            <div className="flex flex-wrap gap-1">
              {pizza.toppings.slice(0, 3).map((topping, index) => (
                <Badge key={index} variant="secondary" className="text-xs">
                  {topping}
                </Badge>
              ))}
              {pizza.toppings.length > 3 && (
                <Badge variant="outline" className="text-xs">
                  +{pizza.toppings.length - 3} more
                </Badge>
              )}
            </div>
            
            <div className="flex items-center justify-between">
              <div className="space-y-1">
                <div className="text-sm text-gray-500">Starting from</div>
                <div className="text-2xl font-bold text-gray-900">
                  ${pizza.sizes.small}
                </div>
              </div>
              
              <Button 
                onClick={() => setShowCustomizer(true)}
                className="bg-black hover:bg-gray-800 text-white px-6"
              >
                Customize
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      <PizzaCustomizer
        pizza={pizza}
        isOpen={showCustomizer}
        onClose={() => setShowCustomizer(false)}
      />
    </>
  );
};
